import { useState } from "react";

function Con(props){
    return(
        <div className={`con no${props.i}`} onClick={()=>{
            props.setModal(props.content[props.i].name);
            console.log(props.modal)
        }}>
            <div className="image"></div>
            <div className="text">
                <p className="pro">{props.content[props.i].project}</p>
                <h3 className="pro-name">{props.content[props.i].name}</h3>
                <p className="pro-page">{props.content[props.i].page}</p>
                <p className="pro-per">기여도 : {props.content[props.i].percent}</p>
                <p className="pro-tool">사용툴 : {props.content[props.i].tool}</p>
            </div>
        </div>
    )
}

export default Con;