import { useState } from "react";
import Con from "./content";
import Popup from "./popup";
import PopupMetro from "./popup";

function Web(props){
    let [content, setContent] = useState(
        [{
            name: '삼아건설',
            project: '개인프로젝트',
            page: '메인페이지, 서브페이지 5',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '메트로안과',
            project: '개인프로젝트',
            page: '메인페이지, 서브페이지 2',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '포트폴리오 사이트',
            project: '개인프로젝트',
            page: '메인페이지, 서브페이지 6',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '금호타이어',
            project: '팀프로젝트',
            page: '메인페이지, 서브페이지 공통부분',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode'
        }, {
            name: '라라수피아 메타버스 수목원',
            project: '팀프로젝트',
            page: '메인페이지, 서브페이지 2, 3D메타버스 14',
            percent: '3D[봄(100%), 겨울(100%), 로비(50%)]',
            tool: 'Figma, Photoshop, Illustrator, VScode, 3Dmax'
        }, {
            name: 'Todo App',
            project: '개인프로젝트',
            page: '메인페이지',
            percent: '100%',
            tool: 'Figma, VScode'
        }]
    )
    let [modal, setModal] = useState(false);

    return(
        <div className="web">
            <div className="container">
                <h2>Web-Project</h2>
                <div className="wrap">
                    {
                        content.map((b, i)=>{
                            return(
                                <Con b={b} i={i} content={content} key={i} modal={modal} setModal={setModal}></Con>
                            )
                        })
                    }
                </div>
                {
                    modal == '메트로안과' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '삼아건설' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '라라수피아 메타버스 수목원' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == '금호타이어' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
                {
                    modal == 'Todo App' ? <Popup modal={modal} setModal={setModal}></Popup> : ''
                }
            </div>
        </div>
    )
}


export default Web;