import { useEffect, useState } from 'react';
import { Routes, Route, Link, useNavigate, useLocation } from 'react-router-dom';

// Left_iconarea 이미지
import icon1 from '../images/left_iconbar_1.png';
import icon2 from '../images/left_iconbar_2.png';
import icon3 from '../images/left_iconbar_3.png';
import icon4 from '../images/left_iconbar_4.png';
import icon5 from '../images/left_iconbar_5.png';
// Left_opentab 이미지
import icon6 from '../images/left_opentab1.png';
import icon7 from '../images/left_opentab2.png';
// Top_opentab 이미지
import icon8 from '../images/top_opentab1.png';

function Top_header() {
    return (
        <div className="top_header">
            <nav>
                <ul>
                    <li><div></div></li>
                    <li>파일</li>
                    <li>편집</li>
                    <li>선택 영역</li>
                    <li>보기</li>
                    <li>이동</li>
                    <li>실행</li>
                </ul>
            </nav>
            <div><h1>김우진.zip</h1></div>
        </div>
    );
}

function Left_iconarea() {
    return (
        <div className="left_iconarea">
            <div><img src={icon1} alt=""></img></div>
            <div><img src={icon2} alt=""></img></div>
            <div><img src={icon3} alt=""></img></div>
            <div><img src={icon4} alt=""></img></div>
            <div><img src={icon5} alt=""></img></div>
        </div>
    );
}

function Left_opentap(props) {
    const handleListItemClick = (event, index) => {
        let copy = [...props.openfile];
        if (!copy.includes(props.fileData[index])) {
            copy.push(props.fileData[index]);
        }
        props.setopenfile(copy);
    };

    return (
        <div className="left_opentap">
            <div className="tool">
                <h3>탐색기</h3>
                <div>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
            <div className="file_name">
                <div>
                    <img src={icon6} alt=""></img>
                </div>
                <h2>김우진.zip</h2>
            </div>
            <nav>
                <ul>
                    {props.fileData.map(function (b, i) {
                        return (
                            <Link to={props.link[i]} key={i}>
                                <li className="file_list" onClick={(event) => handleListItemClick(event, i)}>
                                    <div>
                                        <img src={icon7} alt=""></img>
                                    </div>
                                    <p>{b}</p>
                                </li>
                            </Link>
                        );
                    })}
                </ul>
            </nav>
        </div>
    );
}

function Top_opentap(props) {
    let navigate = useNavigate();
    let location = useLocation();
    let nowLocation = location.pathname;
    
    return (
        <div className="top_opentab">
            <nav>
                <ul>
                    {props.openfile.map(function (b, i) {
                        return (
                            <li className="file_list_top" key={i} id={b}>
                                <div>
                                    <img src={icon7} alt=""></img>
                                </div>
                                <p
                                    onClick={(event) => {
                                        if (b === 'Home') navigate('/');
                                        if (b === 'Profile') {navigate('/profile')};
                                        if (b === 'Web-Project') navigate('/web');
                                        if (b === 'UI/UX') navigate('/uiux');
                                        if (b === 'Design') navigate('/design');

                                        document.querySelectorAll('.file_list_top').forEach((element) => {
                                            element.classList.remove('on');
                                        });
                                        event.target.parentNode.classList.add('on');
                                        // console.log('Current Path:', location.pathname);
                                    }}>{b}</p>
                                <div onClick={() => {
                                    let copy = [...props.openfile];
                                    copy.splice(i, 1);
                                    props.setopenfile(copy);
                                }}>
                                    <img src={icon8} alt=""></img>
                                </div>
                            </li>
                        );
                    })}
                </ul>
            </nav>
        </div>
    );
}


export { Top_header, Left_iconarea, Left_opentap, Top_opentap };
