import a from '../images/a.png'; //html
import b from '../images/b.png'; //css
import c from '../images/c.png'; //boot
import d from '../images/d.png'; //js
import e from '../images/e.png'; //jquery
import f from '../images/f.png'; //react
import g from '../images/g.png'; //redux
import h from '../images/h.png'; //ps
import i from '../images/i.png'; //illu
import j from '../images/j.png'; //premer
import k from '../images/k.png'; //xd
import l from '../images/l.png'; //figma
import m from '../images/m.png'; //max
import o from '../images/o.png'; //after
import { useState } from "react";
import Con from "./content";

function Uiux(props){

    let [content, setContent] = useState(
        [{
            name: '폴바셋',
            project: '팀프로젝트',
            page: '폴바셋 어플 리뉴얼',
            percent: 'UX50%',
            tool: 'Figma, Photoshop',
            icon: [l, h]
        }, {
            name: '반려동물 샬라샬라',
            project: '팀프로젝트',
            page: '반려동물 샬라샬라 기획, 디자인, 퍼블리싱',
            percent: '100%',
            tool: 'Figma, Photoshop, Illustrator, VScode',
            icon: [a, b, d, h, i]
        }]
    )

    return(
        <div className="uiux">
            <div className="container">
                <h2>UI / UX</h2>
                <div className="wrap">
                {
                    content.map((b, i)=>{
                        return(
                            <Con b={b} i={i} content={content} key={b.name}></Con>
                        )
                    })
                }
                </div>
            </div>
        </div>
    )
}

export default Uiux;