// eslint-disable-next-line
import { useState, useNavigate, useEffect } from 'react';
import './App.css';
import {Top_header, Left_iconarea, Left_opentap, Top_opentap} from './components/fixed_headers';
import Web from './components/web.js'
import Uiux from './components/uiux.js'
import Profile from './components/profile.js'
import Design from './components/design.js'
import Etc from './components/etc.js'
import Error from './components/error.js'
import None from './components/none.js';
import {Routes, Route, Link, useLocation} from 'react-router-dom'

function App() {

    let [fileData, setfileData] = useState(['Home','Profile', 'Web-Project', 'UI/UX', 'Design']);
    let [openfile, setopenfile] = useState(['Home']);
    let [link, setlink] = useState(['/', 'profile', 'web', 'uiux', 'design']);
    let [link1, setlink1] = useState([]);
    let location = useLocation();
    let [nowlocation, setNowocation] = useState(location.pathname);
    let [viewWidth, SetviewWidth] = useState(window.innerWidth);
    // 반응형 경고
    useEffect(()=>{
        SetviewWidth(window.innerWidth);
    }, [])
    useEffect(()=>{
        console.log(viewWidth);
        if (viewWidth < 1920) {alert('본 사이트는 반응형이 지원되지 않습니다. PC(해상도 1920*1080이상)로 접속할 것을 권장합니다.')}
    }, [viewWidth]);
    

    return (
        <div className="App">
            <Top_header />
            <Left_iconarea />
            <Left_opentap fileData={fileData} setfileData={setfileData} openfile={openfile} setopenfile={setopenfile} link={link} setlink={setlink} link1={link1} setlink1={setlink1}/>
            <Top_opentap openfile={openfile} setopenfile={setopenfile} link={link} setlink={setlink} link1={link1} setlink1={setlink1}/>
            <Routes>
                <Route path='/' element={<None />}></Route>
                <Route path='/profile' element={<Profile />}></Route>
                <Route path='/web' element={<Web />}></Route>
                <Route path='/uiux' element={<Uiux />}></Route>
                <Route path='/design' element={<Design></Design>}></Route>
                <Route path='/etc' element={<Etc />}></Route>
                <Route path='*' element={<Error />}></Route>
            </Routes>
        </div>
    );
}



export default App;
